import { getArticle, createArticle, updateArticle } from '@/api/article'
export default {
  middleware: 'authenticated',
  name: 'editorIndex',
  data() {
    return {
      article: {
        title: '',
        description: '',
        body: '',
        tagList: []
      },
      tags: '',
      errors: '',
      addDisabled: false,
      slug: this.$route.params.slug
    }
  },
  async mounted() {
    try {
      const { data } = await getArticle(this.slug)
      this.article = data.article
    } catch(err) {
      this.errors = err.message
    }
  },
  methods: {
    async onSubmit() {
      this.addDisabled = true
      try {
        const request = this.slug ? updateArticle : createArticle
        console.log(this.article)
        const { data } = await request({article: this.article}, this.slug)
        const { slug } = data.article
        this.$router.push({
          name: 'article',
          params: {
            slug
          }
        })
      } catch(err) {
        this.errors = err.message
      }
      this.addDisabled = false
    },

    addTag(tag) {
      if (this.article.tagList.indexOf(tag) == -1) {
        this.article.tagList.push(tag)
      }
      this.tags = '' 
    },

    deleteTag(_index) {
      this.article.tagList.splice(_index, 1)
    }
  }

};
